<template>
    <div class="container">
        <div class="row">
            <div v-for="(item, index) in all_score" :key="index" class="col">
                <div class="card" @click="current_user=index"
                     :class="[current_user === index ? 'border-primary border-2' : '']">
                    <div class="card-header text-2xl">
                        {{ item.name }}
                    </div>
                    <div class="card-body text-4xl">
                        {{ item.score }}
                    </div>
                </div>
            </div>
        </div>
        <br>
        <br>
        <br>
        <br>
        <div class="row">
            <div class="col">
                <div class="grid grid-cols-3 gap-3">
                    <div class="ball-container" @click="add(1)">
                        <div class="ball-inner bg-red-600"></div>
                    </div>
                    <div class="ball-container" @click="add(2)">
                        <div class="ball-inner bg-yellow-400"></div>
                    </div>
                    <div class="ball-container" @click="add(3)">
                        <div class="ball-inner bg-green-500"></div>
                    </div>
                    <div class="ball-container" @click="add(4)">
                        <div class="ball-inner bg-yellow-900"></div>
                    </div>
                    <div class="ball-container" @click="add(5)">
                        <div class="ball-inner bg-blue-500"></div>
                    </div>
                    <div class="ball-container" @click="add(6)">
                        <div class="ball-inner bg-red-300"></div>
                    </div>
                    <div class="ball-container" @click="add(7)">
                        <div class="ball-inner bg-black"></div>
                    </div>
                    <div class="ball-container col-span-2 bg-red-200" @click="foul">
                        犯規
                    </div>
                </div>
            </div>
        </div>
        <br>
        <br>
        <br>
        <div class="row">
            <div class="col">
                <button class="btn btn-outline-primary" @click="reset">reset</button>
            </div>
            <div class="col">
                <button class="btn btn-outline-primary" @click="isShow=true">調整分數</button>
            </div>
        </div>
        <Keyboard v-if="isShow" @ok="confirm" @cancel="cancel"></Keyboard>
    </div>
</template>

<script>
export default {
    name: "poll",
    components: {
        Keyboard: () => import('./Keyboard'),
    },
    data() {
        return {
            isShow: false,
            current_user: 0,
            all_score: [
                {
                    name: 'A',
                    score: 0,
                },
                {
                    name: 'B',
                    score: 0,
                }
            ],
        }
    },
    methods: {
        /**
         * 加分
         *
         * @param num {number}
         */
        add(num) {
            this.all_score[this.current_user].score += num;
        },
        /**
         * 犯規
         */
        foul() {
            this.all_score.forEach((item, index) => {
                if (this.current_user !== index) {
                    item.score += 4;
                }
            })
        },
        /**
         * reset all score
         */
        reset() {
            this.all_score.forEach(item => {
                item.score = 0;
            })
        },
        /**
         * close keyboard
         */
        cancel() {
            this.isShow = false;
        },
        /**
         * keyboard press confirm
         *
         * @param event {{id:string, data: string}}
         */
        confirm(event) {
            console.log(event);
            this.all_score[this.current_user].score = parseInt(event.data);
            this.cancel();
        }
    }
}
</script>

<style scoped>
.ball-container {
    @apply flex justify-center items-center border border-black p-2 rounded-md
}
.ball-inner {
    @apply rounded-full w-8 h-8
}
</style>